                </v-card-media>
                <v-card-title primary-title>
                  <div>
                    <div class="title">{{course.title}}</div>
                    <span class="grey--text">{{course.description}}</span>
                  </div>
                </v-card-title>
                <v-card-actions>
                  <v-btn flat color="secondary" @click.native="course.show = !course.show">Course Contents</v-btn>
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
              <v-dialog
              v-model="course.show"
              fullscreen
              hide-overlay
              transition="dialog-bottom-transition"
              >
                <v-card light>
                  <v-card-title class="primary title white--text">
                    <div class="headline">{{course.title}}</div>
                    <v-spacer></v-spacer>
                    <v-btn icon dark @click.native="course.show = false">
                      <v-icon>close</v-icon>
                    </v-btn>
                  </v-card-title>
                  <v-container grid-list-sm class="pa-4">
                    <v-layout row wrap>
                      <v-flex xs12 align-center justify-space-between>
                         <v-list-tile v-for="(item, i) in course.content" :key="i" avatar >
                        <v-list-tile-action>
                          {{ i + 1 }}
                          <!-- <v-icon v-if="item.icon" color="pink">star</v-icon> -->
                        </v-list-tile-action>
                        <v-list-tile-content>
                          <v-list-tile-title v-text="item" class="subheading"></v-list-tile-title>
                        </v-list-tile-content>
                      </v-list-tile>
                      </v-flex>
                    </v-layout>
                  </v-container>
                </v-card>
              </v-dialog>
            </v-flex>
          </v-layout>
        </v-container>
      </v-flex>
    </v-layout>
  </v-container>
