  import { VDialog, VTextField } from 'vuetify'

  export default {
    components: {
      VDialog,
      VTextField
    },
    data () {
      return {
        show: false,
        courses: [
          {
            title: 'STOCK BASICS',
            description: '',
            show: false,
            content: [
              'Basics of investing',
              'Merits of Investing',
              'Introduction to Stock Market',
              'Stock Market and its Functioning',
              'IPO & SHARES',
              'Stock exchanges',
              'Indian stock Exchanges & Indices',
              'Market Regulators',
              'Share Price Fluctuations',
              'Various segments traded',
              'Terminologies',
              'Company Classification',
              'Online Trading',
              'Trading/Demat account',
              'Brokers and Trading Platform',
              'Various tools of Information',
              'Types of Analysis',
              'Psychological requirements',
              'Risk and money management',
              'Career opportunities in stock market'
