            content: [
              'Introduction',
              'Background and basis',
              'Chart types',
              'Candle stick',
              'Candle stick patterns',
              'Chart setups',
              'Some Indicators and oscillators',
              'Trading Strategies',
              'Psychology and Risk'
            ]
          },
          {
            title: 'COURSE DURATION',
            description: '',
            show: false,
            content: [
              'Introduction and basics',
              'Terminologies',
              'Annual reports & results',
              'Profit and Loss statements',
              'Balance sheets',
              'Ratios',
              'Cash Flow Statement',
              'Share holders and promoters',
              'YOY data analysis',
              'Selection Criteria'
            ]
          },
          {
            title: 'POST COURSE SUPPORT',
            description: '',
            show: false,
            content: [
              'Introduction and basics',
              'Terminologies',
              'Annual reports & results',
              'Profit and Loss statements',
              'Balance sheets',
              'Ratios',
              'Cash Flow Statement',
              'Share holders and promoters',
              'YOY data analysis',
              'Selection Criteria'
            ]
          }
        ]
      }
    }
  }
</script>



